import React, { useEffect, useState } from "react";

import { Row, Col, Input, Card, Pagination, Spin, Popconfirm } from "antd";
import { Table, Space } from "antd";
import { RiErrorWarningLine } from "react-icons/ri";
import { connect } from "react-redux";

import MasterPageTitle from "../../../../layout/components/content/master-page-title";
import {
  getOccupations,
  occupationEdit,
  occupationDelete,
  isModelVisible,
  SetOccupationsParams,
} from "../../../../store/Masters/Occupations/occupationsSlice";
import AddOccupations from "./addoccupations";

function Occupations(props) {
  const { Search } = Input;
  useEffect(() => {
    props.getOccupations({ page: 1, search: "", page_size: 10 });
  }, []);

  const columns = [
    {
      title: "S.No",
      dataIndex: "index",
      width: "5%",
      // sorter: (a, b) => a.index - b.index,
    },
    {
      title: "Code",
      dataIndex: "code",
      filterSearch: true,
      width: "35%",
    },
    {
      title: "Occupation Name",
      dataIndex: "name",

      filterMode: "tree",
      filterSearch: true,
      width: "40%",
    },
    {
      title: "Actions",
      key: "action",
      sorter: false,
      width: "10%",
      render: (row, record) => (
        <Space size="middle">
          {props.userAccessList?.indexOf("Masters.change_occupation") !== -1 && (
          <i
            className="ri-edit-box-line hp-mr-2 "
            style={{ cursor: "pointer", color: "#fc6f03" }}
            onClick={() => {
              props.occupationEdit(row);
              props.isModelVisible(true);
            }}
          ></i>
          )}

        {props.userAccessList?.indexOf("Masters.delete_occupation") !== -1 && (
          <Popconfirm
            placement="topRight"
            title="Are you sure to delete this Record?"
            onConfirm={() => props.occupationDelete(record.id)}
            okText="Yes"
            cancelText="No"
            className="hp-ml-8"
            icon={
              <RiErrorWarningLine className="remix-icon hp-text-color-primary-1" />
            }
          >
            <i
              className="ri-delete-bin-2-line "
              style={{ cursor: "pointer", color: "#ff0000" }}
            ></i>
          </Popconfirm>
        )}
        </Space>
      ),
    },
  ];

  const data = props.occupationslist.map((row, key) => ({
    ...row,
    index:
      (props.occupationsParams.page - 1) * props.occupationsParams.page_size +
      key +
      1,
  }));

  function onPageChange(page, pageSize) {
    props.SetOccupationsParams({
      ...props.occupationsParams,
      page_size: pageSize,
      page: page == 0 ? 1 : page,
    });
    props.getOccupations({
      ...props.occupationsParams,
      page_size: pageSize,
      page: page == 0 ? 1 : page,
    });
  }

  function onSearch(val) {
    props.SetOccupationsParams({ ...props.occupationsParams, search: val });
    props.getOccupations({ ...props.occupationsParams, search: val });
  }
  function showTotal(total) {
    return "Total " + total + " items";
  }
  return (
    <Row gutter={[32, 32]} className="hp-mt-32">
      <MasterPageTitle
        pageTitle="Occupations Master"
        addModelTile={
          props.userAccessList?.indexOf("Masters.add_occupation") !== -1 &&
          "Add Occupation"
        }
        goBack={true}
        setIsModalVisible={props.isModelVisible}
        isModalVisible={props.model}
      />

      <Col span={24}>
        <Card>
          <Row className="hp-mb-12">
            <Col span={12}>
              <Search
                placeholder="Search..."
                allowClear
                onChange={(e) => {
                  if (e.target.value == "") {
                    props.getOccupations({
                      ...props.occupationsParams,
                      page: 1,
                      search: "",
                    });
                    props.SetOccupationsParams({
                      ...props.occupationsParams,
                      page: 1,
                      search: "",
                    });
                  }
                }}
                onSearch={(value) => {
                  onSearch(value);
                }}
              />
            </Col>

            <Col span={24}>
              <Spin tip="Loading..." spinning={props.loading} delay={500}>
                <Table
                  columns={columns}
                  dataSource={data}
                  scroll={{ x: 500 }}
                  pagination={false}
                  size="middle"
                />
              </Spin>
            </Col>
            <Col span={24}>
              <Pagination
                size="small"
                current={props.occupationsParams?.page}
                pageSize={props.occupationsParams?.page_size}
                total={props.listCount}
                showTotal={showTotal}
                showSizeChanger
                onChange={onPageChange}
                on
                className="hp-mb-12 hp-mt-24 hp-float-right"
              />
            </Col>
          </Row>
        </Card>
      </Col>
      <AddOccupations />
    </Row>
  );
}

function mapStateToProps({ occupations, system }) {
  return {
    occupationslist: occupations.occupationslist,
    noofpages: occupations.noofpages,
    listCount: occupations.listCount,
    occupationsParams: occupations.occupationsParams,
    loading: occupations.loading,
    userAccessList: system.userAccessList,
  };
}

export default connect(mapStateToProps, {
  getOccupations,
  occupationEdit,
  occupationDelete,
  isModelVisible,
  SetOccupationsParams,
})(Occupations);
